.contact-tab {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  .hotline_container {
    display: flex;
    margin-bottom: 10px;
    align-items: center;
  }

  .address_container {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }

  .email_container {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }
  .address_text,
  .hotline_text,
  .email_text {
    font-family: 'Google Sans';
    font-weight: 400;
    color: black;
    font-size: 15px;
    margin-left: 15px;
  }

  .divider {
    margin-top: 15px;
    margin-bottom: 25px;
    width: 100%;
    height: 2px;
    background-color: #ecedef;
  }

  .faq_section_title {
    font-family: 'Google Sans';
    font-weight: 700;
    color: black;
    font-size: 17px;
  }

  .faq_container {
    cursor: pointer;
    margin-bottom: 8px;
    margin-top: 8px;
    padding: 12px 24px;
    display: flex;
    flex-direction: column;
    background: #ffffff;
    border: 1px solid #dfe2e7;
    box-sizing: border-box;
    border-radius: 4px;
  }

  .title_faq_container {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }

  .question_text {
    flex: 1;
    font-family: 'Google Sans';
    font-weight: 700;
    color: black;
    font-size: 17px;
  }

  .answer_text {
    font-family: 'Google Sans';
    font-weight: 400;
    color: black;
    font-size: 15px;
  }
}
