.user-tabs {
  display: flex;
  justify-content: flex-start;
  .form-builder {
    display: flex;
    flex-direction: column;
    margin-right: 0;
    padding-left: 0;
  }

  .user-info-show {
    display: flex;
    width: 100%;
  }
  .info-container {
    flex: 1;
  }
  .edit-info {
  }
  .edit_info_title {
    font-family: 'Google Sans';
    font-weight: 600;
    display: flex;
    align-items: center;
    color: black;
    font-size: 20px;
    margin-bottom: 25px;
    margin-left: 15px;
  }
  .makeStyles-root-19 {
    padding: 12px 12px 0px 12px;
    border: 1px solid #a6aebb;
  }
  .edit_user_info_identify,
  .edit_user_info_old_password,
  .edit_user_info_new_password,
  .edit_user_info_confirm_password,
  .edit_user_info_phone,
  .edit_user_info_dob,
  .edit_user_info_email,
  .edit_user_info_address,
  .edit_user_info_name {
    display: flex;
    font-family: 'Google Sans';
    font-weight: 400;
    align-items: center;
    padding: 6px;
    input:focus {
      box-shadow: none;
    }
  }
  .old_password_input,
  .confirm_password_input,
  .new_password_input {
    border: 1px solid #eeeeee;
    input:focus {
      box-shadow: none;
    }
  }

  .edit_user_info_phone {
    background-color: #f6f6f6;
  }
  .user-avatar,
  .ic-default-avatar {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 48px;
    margin-right: 10px;
    path {
      fill: #c4c4c4;
    }
  }

  .change_password {
    font-family: 'Google Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    color: #0443c6;
    cursor: pointer;
  }
  .change_password:hover {
    text-decoration: underline;
  }

  .user-name {
    font-family: 'Google Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    color: black;
  }
  .info-row {
    font-family: 'Google Sans';
    font-style: normal;
    display: flex;
    align-items: center;
    color: black;
    font-size: 18px;
    width: 100%;
    margin-bottom: 15px;
  }
  .info-label {
    width: 200px;
  }
  .info-content {
    flex: 1;
  }
  .form-widget {
    display: flex;
    align-items: center;
  }
  .icon-button {
    font-family: 'Google Sans';
    font-style: normal;
    color: black;
    path {
      fill: red;
    }
  }
  .edit_user_info_submit {
    background-color: #fe5466;
    padding: 12px 30px;
    font-family: 'Google Sans';
    color: white;
    font-size: 15px;
    font-weight: 700;
    border-radius: 4px;
  }

  .change_password_container {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .change_password_title {
    font-family: 'Google Sans';
    margin-left: 14px;
    font-size: 20px;
    font-weight: 700;
  }

  .confirm_password_title,
  .current_password_title,
  .new_password_title {
    font-family: 'Google Sans';
    font-size: 15px;
    font-weight: 400;
  }

  .input {
    width: 100%;
    height: 32px;
    border: 1px solid #eeeeee;
    box-sizing: border-box;
    border-radius: 4px;
  }

  .change_password_error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    margin-top: 8px;
    color: #fe5466;
  }

  .change_password_submit {
    margin-top: 30px;
    background-color: #fe5466;
    padding: 12px 30px;
    font-family: 'Google Sans';
    color: white;
    font-size: 15px;
    font-weight: 700;
    border-radius: 4px;
  }

  @media screen and (max-width: 600px) {
    .info-label {
      width: auto;
    }
  }
}
